import { useRef, useState, useEffect } from "react";
import SpeechRecognition, { useSpeechRecognition } from "react-speech-recognition";
import { useSpeechSynthesis } from 'react-speech-kit';
import Speech from 'react-speech';
import { Card } from "@material-ui/core";
import CardDragons from './CardDragons';
import CardTwoFour from "./CardTwoFour";
import CardOdd from "./cardOdd";
import CardOneThree from './CardOneThree';
import CardYear from './CardYear'
import CardSinglesAndPairs from './CardSinglesAndPairs'
import CardLike from './CardLike'
import CardQuints from './CardQuints'
import CardThree from './CardThree'


// program to reverse a string

function reverseString(str) {

    // return a new array of strings
    const arrayStrings = str.split(" ");
   
    // reverse the new created array elements
    const reverseArray = arrayStrings.reverse();
 
    // join all elements of the array into a string
    const joinArray = reverseArray.join("");
    
    // return the reversed string
    return joinArray;
}

export default function DummyFunc() {
  const { transcript, resetTranscript } = useSpeechRecognition();
  const [isListening, setIsListening] = useState(false);
  const microphoneRef = useRef(null);
  const { cancel } = useSpeechSynthesis();

  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return (
      <div>
        Browser is not Support Speech Recognition.
      </div>
    );
  }

  const handleListing = () => {


    if (isListening == true){
      setIsListening(false)
      microphoneRef.current.classList.remove("listening");
      SpeechRecognition.stopListening();
    }
    else{
      setIsListening(true);
      microphoneRef.current.classList.add("listening");
      SpeechRecognition.startListening({
        continuous: true,
      });
    }

  };
  const stopHandle = () => {
    setIsListening(false);
    microphoneRef.current.classList.remove("listening");
    SpeechRecognition.stopListening();
  };
  const handleReset = () => {
    stopHandle();
    resetTranscript();
  };
  return (
    <>
    <div >
      <div style={{marginLeft:50, justifyContent:'center'}}>
        <div
          className="microphone-icon-container"
          ref={microphoneRef}
          onClick={handleListing}
        >
          {/* <img src={microPhoneIcon} className="microphone-icon" /> */}
        </div>
        <div className="microphone-status">
          {isListening ? null:  <> 
          {reverseString(transcript).split(" ")[0].includes('run') ? <CardOdd text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('single') ? <CardSinglesAndPairs text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('1357') ? <CardOneThree text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('2468') ? <CardTwoFour text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('like') ? <CardLike text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('qui') ? <CardQuints text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('year') ? <CardYear text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('369') ? <CardThree text = {transcript}/> : null }
          {reverseString(transcript).split(" ")[0].includes('dragon') ? <CardDragons text = {transcript}/> : null }

            {/* <p>"Click to start Listening" </p> */}
            </>}
            
        </div>
        
      </div>
      {/* {transcript && (
        <div className="microphone-result-container">
          <div className="microphone-result-text">{transcript}</div>
        </div>
        
      )} */}
      
    </div>
    <button style={{marginBottom:100, marginTop:100, marginLeft:0}} className="microphone-reset btn" onClick={() => {cancel()}}>
            <p onClick={handleReset}>Restart</p>
    </button>
          </>
  );}